import { Component ,Output, EventEmitter} from '@angular/core';
import { NgClass } from '@angular/common';

import { Todo } from '../data-class/todo';

@Component({
    selector:"todo-form",
    template:`
        <input [(ngModel)]="task" placeholder="add new todo here" />
        <button (click)="addNewTask()">add</button>
    `,
    directives:[NgClass]
})

export class TodoFormComponent {
    @Output() addTask_evt=new EventEmitter<Todo>();
    task:string;
  
    addNewTask(){
        if (!(this.task==="")){
            this.addTask_evt.emit({text:this.task,done:false});
            this.task="";
        }
    }
}